import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom'
import CounterParent from '../Counter/CounterParent';
import About from "../../views/TabsRouter/About";
import Login from "../Authentication/Login";
import LoginRedux from "../Authentication/LoginRedux";
import NotFound from "../../views/TabsRouter/NotFound";
import Success from "../../views/TabsRouter/Success";

const Main = () => (
    <main>
        <Switch>
            <Route exact path='/react/task5'/>
            <Route exact path='/react/task5/about' component={About} />
            <Route exact path='/react/task5/counters' component={CounterParent} />
            <Route exact path='/react/task5/login' component={Login} />
            <Route exact path='/react/task5/login-redux' component={LoginRedux} />
            <Route exact path='/react/task5/login-redux/success' component={Success} />
            <Route exact path='/react/task5/404' component={NotFound} />
            <Redirect to='/react/task5/404' />
        </Switch>
    </main>
)

export default Main;